@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
html {
  height: 100%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Raleway", "Montserrat",
    "Spartan", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(#292e39, #292e39, #3b4252);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.body {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.app {
  text-align: center;
}
.header {
  display: flex;
  justify-content: space-between;
  background-color: #2e3440;
  background-color: transparent;
  border-bottom: solid 0px #5e81ac;
  align-items: center;
}

.header__tabs--tab > p {
  color: #eceff4;
  font-size: 20pt;
  margin: 20px;
  position: relative;
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}
.header__title {
  color: #eceff4;
  font-size: 28pt;
  margin: 20px;
  position: relative;
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.header__tabs {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.header__tabs--tab > p::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  bottom: 0;
  left: 0;
  background-color: #5e81ac;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.2s ease-in-out 0s;
  cursor: pointer;
}

.header__tabs--tab > p:hover::before {
  visibility: visible;
  transform: scaleX(1);
}

.header__tabs--active > p::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  bottom: 0;
  left: 0;
  background-color: #5e81ac;
  visibility: visible;
  transform: scaleX(1);
  cursor: pointer;
}

.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5px;
}
.about__profilePicture {
  margin-top: 10px;
  border-radius: 100%;
  border: solid 2px #5e81ac;
  height: auto;
  width: 100%;
  max-width: 400px;
  box-shadow: 10px 10px 32px 0px rgba(0, 0, 0, 0.75);
}

.about > p {
  font-size: 20pt;
  color: #eceff4;
  margin-top: 30px;
  margin-left: 10%;
  margin-right: 10%;
  max-width: 1100px;
}

.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 20px;
}

.projectPreview {
  max-width: 600px;
  color: #eceff4;
  font-size: 20pt;
  display: flex;
  flex-direction: column;
  margin: 10px;
  margin-top: 20px;
  margin-bottom: 0px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 10px 10px 32px 0px rgba(0, 0, 0, 0.75);
}
.projectPreview__title {
  background-color: #3b4252;
  margin: 0px;
  padding: 10px;
}

.projectPreview__img--container {
  position: relative;
  text-align: center;
  background-color: #3b4252;
  transition: all 0.2s ease-in-out 0s;
}

.projectPreview__img {
  width: 100%;
  max-width: 600px;
  margin-bottom: -7px;
}

.projectPreview__img:hover {
  cursor: pointer;
  opacity: 10%;
}

.projectPreview__img--description {
  overflow: wrap;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -20%);
  pointer-events: none;
  color: transparent;
  transition: all 0.2s ease-in-out 0s;
}

.projectPreview__img:hover + .projectPreview__img--description {
  color: #eceff4;
}

.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}

.contact > p {
  font-size: 20pt;
  color: #eceff4;
  margin: 20px;
}

.contactItem {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 10pt;
}

.contactItem > p {
  color: #eceff4;
  font-size: 20pt;
  margin: 20px;
  position: relative;
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.contactItem > p::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  bottom: 0;
  left: 0;
  background-color: #5e81ac;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.2s ease-in-out 0s;
  cursor: pointer;
}

.contactItem > p:hover::before {
  visibility: visible;
  transform: scaleX(1);
}

